import { Component, h, State, Element, Host } from '@stencil/core';
import { IClickCell, ITooltipCell } from '../../../../types/table-cells';
import { CSSProperties } from '../../../../types/styles';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import { BadgeType } from '../../../atui-badge/atui-badge';

export interface ITextStatusCellParams extends ITooltipCell, IClickCell {
    mapValueToText?: (value: any) => string;
    checkShouldShowSimpleText?: (value: any) => boolean; // can either show status span or plain text
    textStyles?: CSSProperties;
    containerStyles?: CSSProperties;
    mapValueToBadge?: (value: any) => string | 'default';

    // This is a function where you want to transform a text into something
    // I have a situation where the value is a number, and i need to compare
    // that number first to something, apply some things and now transform it
    // into a certain string. e.g value: 50, textTransformed to '50ms'
    textTransform?: (params: ICellRendererParams, value: string) => string;
    useIconHeaderTooltip?: boolean;
}

export interface TextStatusValueParam {
    status: string;
}

interface InitParams extends ITextStatusCellParams, ICellRendererParams {}

/**
 * @category Table Cell
 * @description A cell component for displaying a text with a status.
 */

@Component({
    tag: 'atui-text-status-cell',
    shadow: false,
})
export class AtuiTextStatusCellComponent implements ICellRendererComp {
    @Element() el;
    @State() params: InitParams;
    @State() text: string;
    @State() type: BadgeType = 'default';
    @State() showSimpleText = false;

    init(params: InitParams) {
        const { value } = params;

        if (!value) return;

        this.setCellData(params, value);
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(_: InitParams): boolean {
        return true;
    }

    private setCellData(params: InitParams, value: string) {
        this.params = params;
        const badgeType = this.mapValueToBadgeType(params, value as BadgeType);

        this.text = params.mapValueToText
            ? params.mapValueToText(value)
            : value;
        if (params.checkShouldShowSimpleText)
            this.showSimpleText = params.checkShouldShowSimpleText(value);

        this.type = badgeType as BadgeType;
    }

    private mapValueToBadgeType(params: InitParams, value: BadgeType) {
        const badgeType =
            params.mapValueToBadge && params.mapValueToBadge(value);
        return badgeType || 'default';
    }

    render() {
        return (
            <Host class="flex h-full items-center">
                {this.showSimpleText && this.params && (
                    <span class="truncate text-sm">{this.text}</span>
                )}
                {!this.showSimpleText &&
                    this.params &&
                    (this.params.generateTooltip ? (
                        <atui-tooltip
                            position="right"
                            is_visible={true}
                        >
                            <atui-badge
                                slot="tooltip-trigger"
                                type={this.type}
                                label={this.text}
                                onClick={() => {
                                    if (this.params.click)
                                        this.params.click(this.params);
                                }}
                            />
                            <span
                                slot="tooltip-content"
                                class={`${this.params.tooltipClass ?? ''} leading-normal`}
                            >
                                {this.params.generateTooltip(this.params)}
                            </span>
                        </atui-tooltip>
                    ) : (
                        <atui-badge
                            type={this.type}
                            label={this.text}
                            onClick={() => {
                                if (this.params.click)
                                    this.params.click(this.params);
                            }}
                        />
                    ))}
            </Host>
        );
    }
}
